.pd150{
    padding: 24px 150px;
}
.pd116{
    padding: 24px 116px;
}
.pd119{
    padding: 24px 119px;
}
.pic4{
    display: none;
}
.menu-txt{
    display: flex;
    padding: 0 15px;
    margin: 0 auto;
    li{
        width: 100%;
        text-align: center;
        border: 1px solid #DCDCDC;
    }
    a{
       font-size: 32px;
       color: black;
       font-weight: 500;
       padding: 24px 0;
       display: block;
       width: auto;
       &:hover{
        border-bottom: 8px solid #AA0601;
       }
    }
}
.menu-pic{
    display: flex;
    img{
        width: 50%;
        height: 296px;
    }
}

.product{
    h2{
        text-align: center;
        font-size: 42px;
        padding-top: 80px;
    }
    h3{
        text-align: center;
        font-size: 32px;
    }
    img{
        width: 306px;
        height: 168px;
    }
}
.product-txt{
    display: flex;
    justify-content: space-between;
    p{
        font-size: 24px;
    }
    .money{
        color: #AA0601;
        font-size: 24px;
        font-weight: 500;
    }
}
.brown{
    display: inline-block;
    width: 24px;
    height: 24px;
    border-radius: 4px;
    background-color: #5F3E2D;
}
.orange{
    display: inline-block;
    border-radius: 4px;
    width: 24px;
    height: 24px;
    background-color:#B75929;
}
.option{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 32px;
    row-gap: 80px;
    column-gap: 24px;
    li{
        width: 32%;
        img{
            width: 100%;
        }
    }

}
.nextpage{
    margin-bottom: 80px;
}

.next-btn{
    display: flex;
    justify-content: center;
    li{
        border: 1px solid #555555;
        a{
            display: block;
            color: black;
        }
    }
    .number{
        font-size: 18px;
        a{
            padding:12px 16px;
            &:hover{
                background-color: black;
                color: white;
            }
        }
    }
    .up{
        a{
            font-size: 19px;
            padding: 11px 12px;
            &:hover{
                background-color: black;
                color: white;
            }
            }  
        }
    .down{
        a{
            background-color: #F2F2F2;
            font-size: 19px;
            padding: 11px 12px;
            &:hover{
                background-color: black;
                color: white;
            }
        }
}
}
@media screen and (max-width:767px){
    .menu{
        padding: 0;
    }
    .menu-txt{
        padding: 0;
        a{
        font-size: 16px; 
        }
 
    }
    .option{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        row-gap: 24px;
        li{
            width: 100%;
        }
    }
    .menu-pic{
        .pic3{
            display: none;
        }
        .pic4{
            display: block;
        }
    }
}